import React from "react"
// import * as styles from "./noi-thuy-tinh-cao-cap-page.module.scss"
import { MasterLayoutScene } from "@bep43vn/scenes"
import { MainContent } from "@bep43vn/windgets/main-content"
import { LocationRoute } from "@bep43vn/pages/page-routing"
import { graphql, useStaticQuery } from "gatsby"

export const NoiComDienPage: React.FCWithRoute<NoiComDienPropsType> = () => {
  const pageData = usePageData()
  return (
    <MasterLayoutScene
      collectionName="Nồi cơm điện"
      collectionHref={LocationRoute.NoiComDien}
    >
      <MainContent {...pageData}></MainContent>
    </MasterLayoutScene>
  )
}

export const usePageData = (): NoiComDienPropsType => {
  const data = useStaticQuery(graphql`
    query useNoiComDienData($type: String = "noi-com-dien") {
      allMarkdownRemark(
        sort: { fields: [frontmatter___date], order: DESC }
        filter: { fields: { type: { eq: $type } } }
      ) {
        totalCount
        edges {
          node {
            id
            frontmatter {
              title
              price
              comparePrice
              imageThumbnail
              description
              productSale
              collectionName
              date(formatString: "DD MMMM, YYYY")
            }
            fields {
              slug
            }
            excerpt
          }
        }
      }
    }
  `)
  return data
}

export type NoiComDienPropsType = {
  allMarkdownRemark: {
    totalCount: number
    edges: Array<{
      node: {
        id: string
        frontmatter: {
          price: string
          title: string
          comparePrice: string
          productSale: string
          date: string
          description: string
          imageThumbnail: string
        }
        fields: {
          slug: string
        }
        excerpt: string
      }
    }>
  }
}

export default NoiComDienPage
